.z-preset {
    display: flex;
    flex-wrap: wrap;
    max-width: 200px;
    min-width: 200px;
    font-size: 6pt;

    .z-preset-option.empty {
        background-color: black;
    }

    .z-preset-option.selected {
        background-color: white;
        color: black;
    }

    .z-preset-option {
        text-align: center;
        cursor: pointer;
        width: 15px;
        height: 15px;
        border-radius: 2px;
        background-color: gray;
        margin: 4px;
    }
}
