.composite-object.z-object {
    display: flex;

    .inner-custom {
        overflow: visible !important;
        .composite-objects {
            display: flex;
            .custom-subpatch-name {
                display: none;
            }
            
            .z-object.selected {
                box-shadow: none !important;
            }
            .z-object {
                overflow: visible !important;
                position: relative;
                .inlets, .outlets {
                    display: none;
                }
            }
        }
    }
}
