.spores-playlister {
    z-index: 10000000000000;
    position: absolute;
    bottom: 15px;
    left: 15px;
    cursor: pointer;

    img {
        width: 40px;
        height: 40px;
    }
}

.spores-playlister.showing {
    overflow: hidden;
    height: 100px;
    animation-name: fade-in-menu;
    backdrop-filter: blur(8px);
    animation-timing: ease-in;
    animation-duration: 0.6s;
    width: 200px;
    border-radius: 8px;
    padding: 10px;
    color: white;
    background-color: #22242c38;
    background-image: linear-gradient(45deg, #171718, #55424200);

    .arrow-down {
        position: absolute;
        top: 5px;
        right: 15px;

        cursor: pointer;
        border: solid #99a4a7;
        border-width: 0 3px 3px 0;
        display: inline-block;
        padding: 3px;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);

    }

    .song-item:hover {
        background-color: #ffffff2f;
    }

    .song-item.current{  
        color: black;
        background-color: white;
    }
    .artist-name {
        color: #aab7ba;
        font-size: 10pt;
        margin-bottom: 5px;
    }
    .song-item {
        font-size: 9pt;
        display: flex;
        padding: 2px;

        .pulse-loader {
            margin-top: 3px;
            margin-left: 10px;
        }
    }
}

@keyframes fade-in-menu {
    from {
        height: 0px;
        opacity: 0;
    }

    to {
        height: 100px;
        opacity: 1;
    }
}
