
.autocomplete-messages.empty {
    display: none;
}

.autocomplete-messages {
    width: 300px;
    max-height: 400px;
    overflow: scroll;
    background-color: black;

    .pub-option:hover {
        background-color: white;
        color: black;
    }
    .pub-option {
        cursor: pointer;
        font-size: 9pt;
        padding: 5px 0px 0px 5px;
        line-height: 20px;
    }
}
