
.param-component {
    font-size: 8pt;
    padding: 5px;

    display: flex;
    flex-wrap: wrap;
    max-width: 300px;
    max-height: 100px;
    overflow: scroll;

    .param-name {
        margin: 2px;
    }
}

.p-graphics-component {
    .canvas-manager {
        height: 200px;
        width: 320px;
        canvas {
            position: absolute;
            top: 0px;
            left: 0px;
        }
    }
}

.params-group {
    display: flex;
    flex-direction: column;

    .plane-search-container {
        input {
            height: 25px;
        }
    }
    .p-tabs {
        display: flex;

        .parameters-count {
            margin-left: auto;

            display: flex;

            .hollow-dot {
                margin-left: 5px;
                margin-right: 5px;
                margin-top: 7px;
                width: 10px;
                height: 10px;
                border: 2px solid white;
                border-radius: 14px;
            }
        }
        .tab.show {
            .name {
                background-color: white;
                img {
                    filter: invert(1);
                }
            }
        }
        .tab {
            display: flex;
           .name {
                img {
                    width: 15px;
                    height: 15px;
                    margin: auto;
                    margin-top: 3px;
                }
                background-color: transparent; //white;
                border-radius: 5px;
                color: black;
                height: 20px;
                width: 20px;
                font-size: 10px;
                line-height: 20px;
                cursor: pointer;
            }
            .tab-left {
            }
        }
    }

    .plane-results.parameters-list {
        max-height: 200px;
    }

    .p-toolbar {
        display: flex;
        margin-bottom: 5px;
        
        .unmap-button.selected {
            background-color: white;
            color: black;
        }

        .unmap-button {
            font-size: 8pt;
            margin: 3px;
            padding: 5px 10px 5px 10px;
            background-color: #141414;
            border-radius: 10px;
            cursor: pointer;

            .dot {
                margin-top: 5px;
                margin-left: 5px;
                width: 8px;
                height: 8px;
            }
        }
    }
    .map-buttons {
        display: flex;

        .mappy-btn, .macro-map-button {
            margin: 10px;
            padding: 5px 10px 5px 10px;
            background-color: #141414;
            border-radius: 10px;
            cursor: pointer;
            width: 80px;
        }
    }
}
