.loot-box {
    position: absolute;
    bottom: -18px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1000000000;
    //background-color: red;
    display: flex;
    padding: 20px;
    //backdrop-filter: blur(8px);
    //background-color: #0000003b;
    width: 300px;

    svg {
        width: 300px !important;
    }

    .loot-inner {
        width: 300px;
        flex-wrap: wrap;
        display: flex;
        
        
        .loot-option {
            margin: 3px;
            img {
                width: 60px;
                height: 60px;
            }
        }
    }
}

.bigger.loot-box {
    .loot-inner {
        .loot-option {
            img {
                width: 30px;
                height: 30px;
            }
        }
    }
}
