

.routes-wrapper.showing {
    img.routes-icon {
        background-color: black;
        filter: invert(1);
        box-shadow: 0px 0px 4px 4px white;
    }
}
.routes-wrapper {
    position: relative;
    .routes-editor::before {
        content: '';
        position: absolute;
        top: -20px;
        left: 45px;

        width: 20px;
        height: 20px;
        border-style:solid;
        border-width: 10px;
        border-color: transparent transparent white transparent;
    }

    img.routes-icon {
        width: 15px;
        margin-left: auto;
        margin-right: auto;
    }
    .routes-editor {
        position: absolute;
        bottom: -160px;
        left: -25px;
        z-index: 100000;
        background-color: black;
        width: 250px;
        height: 140px;
        padding: 10px;

        
        .grid-toolbar {
            .dot, .dash {
                display: none;
            }
        }
        .grid {
            width: 130px;
            display: flex;
            flex-wrap: wrap;
            .grid-option.empty {
            }
            .cell.on.grid-option {
                background-color: white;
            }
            .grid-option {
                margin: 3px;
                border: 1px solid white;
                width: 30px;
                height: 30px;
            }
        }
        .close-btn {
            position: absolute;
            right: 5px;
            top: 5px;
    }
    }
}
