
.v3.nft-mode {
    .editor {
        background-color: unset !important;
        display: flex;
        flex-direction: column;

        .z-object {
            .custom-subpatch-name {
                display: none;
            }
        }


        .click-to-start {
            cursor: pointer;
            position: absolute;
            z-index: 10000000;
            width: 100%;
            cursor: pointer;
            font-size: 32pt;
            font-family: stem;
            height: 100%;
            display: flex;
            backdrop-filter: unset !important;
            
            .inner-start {
                //padding-top: 55px;
                width: 340px;
                height: 400px;
                margin: auto;
                text-align: center;
                backdrop-filter: unset !important;
                display: flex;

                .start-text:hover {
                    .play-button-start {
                        z-index: 4;
                        transform: scale(1.1);
                        animation-name: play-hovering;
                        animation-duration: 1s;
                    }
                }
                .start-text {
                    //background-color: #0000001f;
                    //backdrop-filter: blur(8px);
                    margin: auto;

                    .play-button-start.blur {
                        position: absolute;
                        top: 0px;
                        left: 0px;
                    }

                    .play-button-start {
                        margin: 10px;
                        border-style: solid;
                        border-width: 27px 0px 27px 54px;
                        border-color: #ff000000 transparent transparent #ffffff;
                    }
                }
            }
        }
    }
}

.nft-viewer {
    overflow: hidden;
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
//    background-color: #090909;

    .expand-icon {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
        cursor: pointer;
        z-index: 10000000;
    }

    .z-object.main-nft {
        position: relative;
        left: unset !important;
        top: unset !important;
        margin: auto;
        border: none !important;
    }

    .z-object {
        .top-left-edge, .bottom-left-edge, .top-right-edge, .bottom-right-edge {
            display: none !important;
        }
        .context-menu, .inlets, .outlets {
            display: none !important;
        }
    }
}

.z-debug {
    position: absolute;
    top: 2px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    font-size: 9pt;
    color: red;
}

@keyframes play-hovering {
    from{
        transform: scale(1.0);
    }
    to{
        transform: scale(1.1);
    }
}

