
.deps {
    .sub-deps {
        margin-left: 10px;
        .dep {
            padding: 5px;
            display: flex;
            margin-left: 10px;
            width: 403px;
            .obj-name {
                margin-right: 10px;
                border-bottom: 1px solid white;
                font-size: 8pt;
            }
            
            .actions {
                margin-left: auto;
                display: flex;
                .action {
                    line-height: 25px;
                    color: white;
                    border-radius: 8px;
                    padding-left: 5px;
                    padding-right: 5px;
                    margin-right: 4px;
                    display: flex;
                    cursor: pointer;
                    background-color: #ffffff26;
                    font-size: 8pt;

                    .checkbox {
                        margin-top: auto;
                        margin-bottom: auto;
                        margin-left: 5px;
                        width: 12px;
                        height: 12px;
                        border: 1px solid black;
                    }
                    .checkbox.checked {
                        background-color: red;
                    }
                }
            }
        }
    }
}
