
.rules-to-ignore.show {
    .toggle-btn {
        background-color: white;
        color: black;
    }
}
.rules-to-ignore {
    position: relative;
    .toggle-btn {
        background-color: black;
    color: white;
    line-height: 30px;
    border-radius: 16px;
    width: 30px;
    height: 30px;
    margin-left: 10px;
    /* padding: 5px; */
    align-items: flex-start;
    text-align: center;
   }

    .rules-dropdown {
        display: flex;
        width: 300px;
        flex-wrap: wrap;
        position: absolute;
        top: 35px;
        padding: 8px;
        background-color: #3d558561;
        backdrop-filter: blur(8px);
        border-radius: 8px;
        
        .close-btn {
            position: absolute;
            top: 5px;
            right: 15px;
            cursor: pointer;
            border-radius: 8px;
        }

        .rule-option.selected {
            background-color: white;
            color: black;
        }

        .rule-option {
            margin: 4px;
        }
        
    }
}
