.z-object {
    .loop-ui {
        position: relative;
        width: 200px;

        .current-step {
            display: flex;
            font-size: 9pt;
            .current-label {
                color: gray;
                margin-right: 5px;
            }
            .step-value {
                color: red;
            }
        }
        .loop-presets {
            display: flex;

            .preset.current {
                background-color: white;
                color: black;
            }
            .preset {
                margin: 5px;
                background-color: black;
                color: white;
                width: 20px;
                height: 20px;
                line-height: 20px;
                text-align :center;
            }

            .new-preset {
                width: 20px;
                height: 20px;
                line-height: 20px;
                text-align :center;
                background-color: black;
                color: green;
                margin: 5px;
            }
        }

        .step-and-quantize {
            display: flex;
            margin-top: 20px;
        }
        .quantize-fields {
            display: flex;
            margin-left: auto;

            .quantize-b {
                cursor: pointer;
                background-color: white;
                color: black;
                border-radius: 5px;
                margin-left: 5px;
                margin-right: 5px;
                font-size: 9pt;
            }

            .next,.prev {
                cursor: pointer;
                border: solid white;
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 3px;
                height: 0px;
                margin-top: 3px;
            }

            .next {
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
            }

            .prev {
                 transform: rotate(135deg);
                 -webkit-transform: rotate(135deg);
            }
        }

        .parameters {
            margin-left: unset;
            float: unset;
            margin-top: 5px;
            display: flex;
            flex-wrap: wrap;
            .param-value:active {
                border-color: gray;
            }
            .param-value {
                width: 80px;
                margin-right: 5px;
                border: 1px solid transparent;
                display: flex;
                font-size: 9pt;
                .param-name {
                    margin-right: 8px;
                    color: gray;
                }
            }
        }
        .track-loop-info {
            margin-top: 20px;
            .inner-loop:hover {
                background-image: linear-gradient(45deg, #59f166, #259e1b);
            }
            .inner-loop, .inner-start {
                height: 16px;
                bottom: -8px;
            }

            .inner-start {
                pointer-events: none;
            }
        }

        .Dropdown-control {
            line-height: 15px;
            font-size: 9pt;
            border: 1px solid black;

        }
        .Dropdown-option {
            font-size: 9pt;
        }

        .track-loop-info {
            margin-top: unset !important;
        }

        .loop-btn.looping {
            background-color: #8080804a;
            border-color: orange;

            .zloop-icon {
                background-color: orange;
            }
        }
        .loop-btn {
            position: absolute;
            top: 5px;
            right: 5px;
            cursor: pointer;
            width: 20px;
            height: 20px;
            border-radius: 20px;
            background-color: #8080804a;
            display: flex;
            border: 3px solid gray;

            .zloop-icon {
                margin: auto;
                border-radius: 2px;
                height: 10px;
                width: 3px;;
                background-color: gray;
            }
        }
    }
}

@keyframes brighten-loop {
    0%{
        //background-image: linear-gradient(45deg, #082c0b, #259e1b);
        background-color: black;
    }

    100% {
        //background-image: linear-gradient(45deg, #59f166, #259e1b);
        background-color: white;
    }
}
