.drum-router {
    .halftime-control:active {
        .numerator, .denominator {
            animation-name: push-it;
            animation-duration: 1s;
            background-color: black !important;
        }
    }
    .halftime-control:hover {
        .numerator, .denominator {
            animation-name: fade-to-purple;
            animation-duration: 0.5s;
            background-color: #28044e;
        }
    }
    .halftime-control {
        display: flex;
        flex-direction: column;
        cursor: pointer;
        .numerator, .denominator {
            padding: 1px;
            font-size: 6pt;
            background-color: black;
            color: white;
            text-align: center;
        }

        .slash {
            margin-left:auto;
            margin-right:auto;
            margin-top: 1px;
            margin-bottom: 1px;
            width: 15px;
            height: 3px;
            background-color: white;
        }
    }
}

@keyframes fade-to-purple {
    from {
        background-color: black;
    }

    to {
        background-color: #28044e;
    }
}

@keyframes push-it {
    from {
        background-color: #28044e;
        transform: scale(1);
    }

    to {
        background-color: black;
        transform: scale(.7);
    }
}
