.progress-bar {
    .loading-text {
        font-family: stem;
    }
}

.circular-container.progress-bar {
    padding: 0px;
    background-color:  unset !important;
    .circular-progress {
        circle.outter-circle {
            stroke: #a4a2a230;
        }
        circle {
            fill: transparent;
        }
    }
}
