
.z-object.jdebug-object {
    transform: scale(.75);
    transform-origin: top left;
}
.jdebug {
    display: flex;
    flex-direction: column;
    margin: 10px;
    border-radius: 20px;
    background-color: black;
    overflow: hidden;

    .debug-figures {
        display: flex;
    }
    
    .figure-drawer {
        display: flex;
        margin: 2px;

        .cell-info {
            display: flex;
            flex-direction: column;
            height: 100%;

            .cell.on {
                background-color: #1f1f1fe3;
                .symbol {
                    background-color: white;
                }
            }
        }
        .cell-data {
            flex: 1;
            display: flex;
            .cell-rules.current.pulse {
                .hand-info {
                    background-color: #ff0fc0 !important;
                }
            }

            .cell-rules.current {
                .hand-info {
                    background-color: #1dffc0 !important;
                }
                .cell {
                    background-color: #1dffc0 !important;
                }
            }
            .cell-rules {
                display: flex;
                flex-direction: column;
                height: 100%;
                .cell {
                    width: 30px;
                    height: 30px;
                    background-color: white;
                    padding: 10px;
                }
            }

            .hand-info {
                padding: 10px;
                margin-top: auto;
                font-size: 5pt;
                width: 30px;
                height: 30px;
                background: black;
                text-align: center;
            }

            .velocity-info {
                width: 30px;
                height: 40px;
                position: relative;

                .vel-indicator {
                    position: absolute;
                    filter: saturate(8);
                    bottom: 0px;
                    left: 0px;
                    width: 30px;
                    background-color: red;
                }
            }
        }
        
        .cell.on {
            background-color: white;
            padding: 10px;
            height: 30px;

            .dash {
                width: 40px;
            }
        }
    }
    
}
