.track-option, .loop-ui {
    .track-loop-info {
        width: 100%;
        position: absolute;
        height: 2px;
        margin-top: 22px !important;
        background-color: #88808061;
        .inner-start {
            position: absolute;
            height: 6px;
            bottom: -2px;
            width: 5px;
            background-color: red;
            background-color: #12121200;
            background-image: linear-gradient(103deg, #ff025d00, #ff002b);
            transform: translate(-15%, 0px);
        }

        .inner-loop {
            position: absolute;
            height: 6px;
            bottom: -2px;
            border: 1px solid #2cff0075;
            background-image: linear-gradient(45deg, #082c0b, #259e1b);
        }
    }
}
