.minimized-subpatches {
    display: flex;

    .subpatch-option:hover {
        animation-name: hover-sub;
        animation-duration: 0.6s;
        filter: brightness(1.3);
    }

    .subpatch-option.viewing {
        background-color: #ffffff47;
        border-color: white;
    }

    .subpatch-option {
        img {
            width: 20px;
            height: 20px;
            margin-right: 5px;
            margin-top: 3px;
        }
        display: flex;
        cursor: pointer;
        background-color: #1b191980;
        border: 1px solid #ffffff14;
        border-radius: 4px;
        padding: 5px 10px 5px 10px;
        color: white;
        font-size: 10pt;
        line-height: 22px;
        margin-left: 10px;
    }
}

@keyframes hover-sub {
    from {
        filter: brightness(1.0);
    }

    to {
        filter: brightness(1.3);
    }
}
