.color-object.z-object {
    width: unset !important;
    height: unset !important;
    min-width: 50px;

    .z-color.showing {
        .color-show-btn {
            box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
        }
        .color-show-btn {
        }

        .chrome-picker {
            position: absolute;
            top: 30px;
            left: 0px;

            label {
                display: none !important;
            }
        }
        svg {
            display: none;
        }
    }
    .z-color {
        .color-show-btn {
            z-index: 100;
            cursor: pointer;
            width: 30px;
            height: 30px;
            border: 1px solid white;
        }
    }
}
