
.z-object {
    .sdf-layers {
        font-size: 10pt;
        background-color: #0000005e;
        color: white;
        padding-top: 10px;

        .toggle-switch {
            margin-top: 8px;
            margin-left: 7px;
            .label {
                font-size: 10pt;
                margin-right: auto;
            }
        }

        .color-field, .sdf-field {
            display: flex;
            min-width: 140px;
            .color-btn {
                width: 20px;
                height: 12px;
                border: 1px solid white;
                margin-top: 0px;
                margin-left: auto;
            }

            .is-gl {
                margin-left: auto;
                background-color: white;
                color: black;
                border-radius: 8px;
                width: 20px;
                text-align: center;
            }
            .z-number {
                width: 40px;
                margin-left: auto;
                display: flex;
                background-color: #272626;
                .arrow-right {
                    margin-right: 5px;
                }
            }
        }

        .sdf-field {
            line-height: 13px;
        }

        .layer.editing {
            background-color: #c4cbff4a;
        }

        .layer {
            border: 1px solid transparent;
            border-top: 1px solid #ffffff3b;
            margin-top: 5px;
            padding-top: 5px;
            padding-bottom: 5px;
            .fields {
                padding-left: 5px;
                .field-name {
                    margin-left: 15px;
                }
            }
        }

        .layer-name:hover {
            .delete-layer {
                display: block;
            }
        }
        .layer-name {
            font-size: 11pt;
            margin-left: 3px;
            position: relative;

            .delete-layer {
                display: none;
                position: absolute;
                top: -4px;
                right: 8px;
                font-size: 16px;
                cursor: pointer;
            }
        }

        .new-layer {
            margin-top: 15px;
            margin-bottom: 15px;
            font-size: 28pt;
            background-color: white;
            color: black;
            text-align: center;
            line-height: 35px;
        }
    }

    .chrome-picker {
        left: 150px;
        top: -100px;
    }
}
